import { useEffect, useState } from 'react';
import { Card } from '../../components/ui';
import './MakeUpPage.scss';
import { selectAllProducts } from '../../redux/products/productsSlice';
import { useAppSelector } from '../../redux/hooks';
import { Products } from '../../types/products.type';
import { SearchGroup } from '../../components';

const MakeUpPage = () => {

  const [productDatas , setProductDatas] = useState<Products[]>();
  const products = useAppSelector(selectAllProducts);


  useEffect(() => {
    
      if(products  ) {        
        const datas = products.filter((e) => e.categories === "make-up");        
        setProductDatas(datas)
      }
      
  },[products])

  return (
    <div className='container-makeup-zone'>

      <section>
        <div className="title-grp">
          <h3> MAQUILLAGE </h3>
          <p>
            Essentiel beaut incontournable, le maquillage est notre meilleur alli ! Teint frais, lvres
            gourmandes et smoky eyes, le makeup parfait est  porte de main. On laisse libre cours
             toutes nos envies maquillage en crant des looks  l'infini ! Best-sellers ou nouveauts
            beaut, le maquillage n'a jamais t aussi fun qu'avec Sephora.
          </p>
        </div>
      </section>

      <section className='search-container-form'>
        <SearchGroup />
      </section>

      <section>
        <ul className='items-list makeup'>
          
           { productDatas?.map((e : Products) => (
            <li key={e.id}>
                  <Card 
                    path={'make-up/'+ e.id}
                    {...e} 
                  />
            </li>
          ))} 
        </ul>
      </section>
    </div>
  )
}

export default MakeUpPage;
